    background-color: #bfbfbf;
  }
  
  .btn1{
    background-color: #484848; 
    border: 2px solid #484848;
    border-radius: 6px;
    color: whitesmoke;
    padding: 10px 10px;
    margin-right: 7px; 
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 17px;
  }
  
  .text1{
    font-weight: 50px;
  }
  
  .MyQR{
    text-align: center;
    padding-top: 50px;
  }
  
  .btn2{
    border: 2px solid #484848;
    font-size: 16px;
    color: #484848;
    border-radius: 6px;
    padding: 10px 10px; 
    text-align: center;
    text-decoration: none;
    display: inline-block;
  }
  
